#heroSection{
    width: 100vw; 
    margin-top: 20vh;
    font-family: 'Mulish', sans-serif;
}

#heroSectionContainer{
    width: 90vw;
    height: 100vh;
    margin: auto;
    display: flex;
}
  
#leftSideHero{
    width: 40vw;
    margin-top: 9vh;
}

#h1Div{
    width: 29vw;
    margin-bottom: -3vh;
}
  
h1{ 
    font-size: 44px;
    font-weight: 700;
    line-height: 53px;
    letter-spacing: 0.02em;
}
  
#heroBtn{
    background: #2F4644;
    width: 126px;
    height: 56px;
    margin-left: .5vw;
    padding: 12px;
    border-radius: 4px;
    gap: 10px;   
    border-width: 0;
    font-size: 16px;
    color: #ffffff; 
    cursor: pointer;
}

#heroBtn:active{
    transform: scale(0.98);
  }
  
#heroArticle{
    margin-bottom: 9vh;
    margin-left: 13.5px;
}

#heroArticle p{
    color: #71605B;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;

}

#searchContainer{
    display: flex;
    width: 60vw;
    margin-top: 0px;
    margin-left: 13.5px;
    border-radius: 4px;
}
  #searchBox{
    display: flex;
    width: 436px;
    height: 56px;
    background-color: #ffffff;
    border-radius: 4px;
}

#heroImgContainer{
    width: 35vw;
    margin-right: 75vw;
   
}
#heroImg{
    display: block;
    width:473px;
    height: 100vh;
    margin-left: 10vw;
    margin-top: -5vh;
}
  
#allCategoriesSearch{
    flex-grow: 1;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 0.01em;
    color: #000000;
    padding: 12px;
    width: 12vw;
    height: 24px;
    background: #FFFFFF;
    border-radius: 10px;
    border-width: 0;
}
  
#allCategoriesSearch:focus{
    outline: 0;
}

#categoriesDiv{
    display: flex;
    width: 12vw;
    margin-right: 0vw;
    border-radius: 10px;
}

#demacator {
    width: 2px;
    background-color: #BDB4B2;
    border: 0px;
    height: 6vh;
    margin: auto;
}
  
#heroSubSection{
    margin-top: 30vh;
    font-family: 'Inter', sans-serif;
}
  
#heroSubSectionHeader{
    text-align: center;
    font-size: 64px;
    font-weight: 700;
    font-style: normal;
    line-height: 72px;
    letter-spacing: 0.02em;
    color: #362A26;
}
  
#promo{
    width: 100vw;
    height: 85vh;
    margin-top: 10vh;  
    margin-bottom: -200px;
