    margin-top: 7vh;
    font-family: 'inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 128px;
    line-height: 100px;
    letter-spacing: 0.02em;
    color: #EB5757;
}

.promoCtaContainer{
    display: flex;
    flex-direction: row;
    margin-top: 20vh;
    margin-left: 7vw;
}

.promoImgContainer{
    width: 35vw;
    margin-top: -50px;
    height: 55vh;
    margin-right: 20vw;
    margin-left: 7.5vw;
}

.promoImg{
