}
.promoCta{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -15vh;
}

.promoDiscount{
    font-family: 'inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 72px;
    letter-spacing: 0.02em;
    color: #CD4723;
}

.promoBtn{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px;
    gap: 10px;
    cursor: pointer;
    width: 176px;
    height: 48px;
    background: #202E2D;
    border-radius: 4px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
}

#categories{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 10vh;
}
  
#categoriesHeader{
    margin-bottom: 13vh;
    text-align: center;
    font-size: 36px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 700; 
    line-height: 45px;
    letter-spacing: 0.02em;
    color: #362A26;
}
  
  #categoriesContainer{ 
    width: 100vw;
}
  
.categoriesList{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90vw;
    margin: auto;
    margin-bottom: 3vh; 
}

.categoriesListDiv{
    display: flex;
    width: 365px;
    height: 206px;
    border-radius: 4px 4px 0px 0px;
}
  
.categoriesListItems{
    height: 266px;
    background-color: #FDF8F7;
    margin-right: 8px;
    padding-bottom: 2vh;
    border-radius: 4px;
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.05);
    cursor: pointer;
}
  
.categoriesListTitle{
    text-align: center;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.01em;
    color: #71605B;
} 

.categoriesListImg{
    display: block;
    width: 232px;
    height: 206px;
    margin: auto;
    border-radius: 4px 4px 0px 0px;
}

