.container{
    width: 100vw; 
    margin-top: 20vh;
    font-family: 'Mulish', sans-serif;
}
#searchContainer{
    width: 100vw;
    margin-left: 55px;
}

#searchBox{
    margin-left: 27px;
    display: flex;
    width: 86.5vw;
    height: 56px;
    background-color: #ffffff;
    border-width: 1px 0px 1px 1px;
    border-style: solid;
    border-color: #E2E9E8;
    border-radius: 4px 0px 0px 4px;
}

#allCategoriesSearch{
    flex-grow: 1;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 0.01em;
    color: #000000;
    padding: 12px;
    width: 12vw;
    height: 24px;
    background: #FFFFFF;
    border-radius: 10px;
    border-width: 0;
}

#allCategoriesSearch:focus{
    outline: 0px;
}

#demacator {
    width: 2px;
    background-color: #BDB4B2;
    border: 1px solid #E6E1E0;  
    height: 6vh;
    margin: auto;
}

#categoriesDiv{
    display: flex;
    width: 12vw;
    margin-right: 4vw;
    border-radius: 10px;
}

.searchBtn{
    padding: 12px;
    width: 126px;
    height: 56px;
    background: #2F4644;
    border: 1px solid #2F4644;
    border-radius: 0px 4px 4px 0px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    cursor: pointer;
}

.searchBtn:active{
    transform: scale(0.98);
}

#hero{
    display: flex;
    height: 258px;
    background: #F4602F;
    border-radius: 4px;
    width: 86.5vw;
    margin-left: 82px;
    margin-top: 20px;
}

#hero h1{
    font-style: normal;
font-weight: 700;
font-size: 44px;
line-height: 53px;
/* or 120% */

letter-spacing: 0.02em;

/* Primary/800 */

color: #FDF8F7;
width: 438px;
height: 106px;
left: 30px;
margin-bottom: 0;
}

#hero p{
    font-weight: 400;
font-size: 16px;
line-height: 24px;
/* or 150% */

letter-spacing: 0.01em;

/* Primary/700 */

color: #FAEEEA;
width: 340px;
height: 48px;
margin-top: 0;
}

#heroArticle{
    margin-left: 48px;
    margin-right: 81px;
    margin-top: 0px;
}

#banner{
    width: 540px;
    height: 258px;
}

#bidHeader{
    width: 86.5;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
    margin-bottom: -30px;
}