nav{
    position: fixed;
    top: 0;
    width: 100vw;
    height: 12vh;
    padding-top: 2vh;
    background-color: #ffffff;
    font-family: 'Inter', sans-serif; 
    z-index: 1;
}

#navContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90vw;
    margin: auto;
}
  
#logo{
    width: 10vw;
    margin-left: -1vw;
    margin-right: 16vw;
}
  
#logoImg{
    width: 10vw; 
}
  
  
#navList{
    display: flex;
    margin-left: 20vw; 
      
}

.navListItem{
    color: #000000;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.01em;
    line-height: 24px;
    margin-right: 5vw;     
}

.navListItem:hover{
    color: #F4602F;
}

.easy::after{
    margin-right: 2vw;
    content:i;   
}

.navBtn{
    background: #F4602F;
    color: #ffffff;
    width: 126px;
    height: 48px;
    font-size: 16px;
    border-width: 0;
    border-radius: 4px;
    padding: 12px;
    gap: 10px;
    cursor: pointer;
}

.navBtn:active{
    transform: scale(0.98);
  }

.navBtn:hover{
    color: #000000;
}