.body{
  background: #F6F4F4;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

ul{
  list-style-type: none;  
}

li{
  cursor: pointer;
}

.btn{
  cursor: pointer;
}

.btn:active{
  transform: scale(0.98);
}

/*login and signup*/
.preLanding{
  margin: 0;
  height: 120vh; 
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  background: #F6F4F4;
}

.preLandingLogo{
  width: 10vw;
  margin-left: 10vw;
  margin-top: 3vh;
  margin-bottom: 5vh;
}

#formContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form{
  width: 40vw;
  height: 65vh;
  box-shadow: 0px 0px 3px 1px rgba(12, 12, 12, 0.05);
  border-radius: 8px;
  margin-bottom: 0;
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.formTitle {
  font-family: 'Mulish', sans-serif;
  color: #362A26;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 45px;
  letter-spacing: 0.02em;
  margin-bottom: 2rem;
}

.formMessage {
  text-align: center;
  margin-bottom: 1rem;
}

.formMessageSuccess{
  color: var(--color-success);
}

.formMessageError{
  color: var(--color-error);
}

::placeholder {
  color: #BDB4B2;
  opacity: 1;
}

.formInputGroup{
  margin-bottom: .7rem;
}

.formInput {
  display: block;
  width: 35vw;
  height: 7vh;
  padding: 0.75rem;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid #dddddd;
  outline: none;
  font-size: 0.9rem;
  background: #ffffff;
  color: #362A26;
  transition: background 0.2s, border-color 0.2s;
}

.fa{
  display: block;
  color: grey;
  margin-top: -5vh;
  margin-left: 33vw;
  cursor: pointer;
}

.formInput:focus{
  border-color: var(--color-primary);
  background: #ffffff;
}

.formInputError {
  color: var(--color-error);
  border-color: var(--color-error);
}

.formInputErrorMessage{
  margin-top: 0.5rem;
  font-size: 0.85rem;
  color: var(--color-error);
}

.formButton {
  background: #F4602F;
  color: #ffffff;
  font-weight: bold;
  font-size: 1.1rem;
  border: none;
  border-radius: 4px;
  outline: none;
  cursor: pointer; 
  padding: 16px;
  width: 35vw;
  margin-top: 3vh;
}

.formButton:active{
  transform: scale(0.98);
}

.formText{
  text-align: center;
}

.formLink{
  text-decoration: none;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #362A26;
}

.formLink:hover{
  text-decoration: underline;
}

.firstCtaGp{
  color: #F4602F;
  
}

label{
  color: #71605B;
  display: block;
  margin-bottom: 0.5rem;
}

.labelLogin{
  margin-bottom: 2vh;
}

